import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Serie } from 'src/app/services/interfaces/serie';
import { DataSerieService } from 'src/app/services/data-serie.service';
import { NavController } from '@ionic/angular';

@Component({
	selector: 'app-info-serie',
	templateUrl: './info-serie.page.html',
	styleUrls: [ './info-serie.page.scss' ]
})
export class InfoSeriePage implements OnInit {
	selectedSerie: Serie;

	constructor(
		private navCtrl: NavController,
		private serviceSerie: DataSerieService,
		private activatedRoute: ActivatedRoute
	) {}

	ngOnInit() {
		this.activatedRoute.paramMap.subscribe((paramMap) => {
			if (!paramMap.has('idSerie')) {
				this.navCtrl.navigateBack([ '/medias/series' ]);
				return;
			}
			this.selectedSerie = this.serviceSerie.getInMySerie(paramMap.get('idSerie'));
		});
	}
	goBack() {
		this.navCtrl.navigateBack([ '/medias/series' ]);
	}

	isFinished(s: Serie) {
		return !(s.dateF === undefined);
	}
}
